.navbar {
  color: white;
  padding: 0px 20px;
  position: sticky;
  z-index: 100;
  top: 0px;
  width: 100%;
  height: 50px;
  background-color: #333;
  display: flex;
  justify-content: space-between;
  .links {
    gap: 10px;
    display: inline-flex;
    justify-content: space-evenly;
    a {
      text-decoration: none;
      color: inherit;
    }
  }
}
.slider {
  overflow: hidden;
  width: 100%;
  height: 450px;
}
.beta{
  color: whitesmoke;
  background-color: red;
  position: fixed;
  top: 50px;
  width: 70px;
  right: 5px;
  padding: 3px;
  z-index:101;
  text-align: center;
  border-radius: 0px 5px;
}